
@import url(~normalize.css/normalize.css);
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  height: 100%;
  background-image: -webkit-gradient(linear, left bottom, right top, from(#15315c), color-stop(#2e5381), color-stop(#4778a8), color-stop(#629ecf), to(#7ec7f6));
  background-image: linear-gradient(to right top, #15315c, #2e5381, #4778a8, #629ecf, #7ec7f6);
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-size: 1em;
  font-family: "Montserrat", sans-serif;
  margin: 0;
  color: white;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.3);
}
h1 {
  text-align: center;
}
h1,
h2,
h3 {
  font-family: "Permanent Marker", cursive;
  letter-spacing: 0.15em;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.8);
  margin-top: 0;
}
a {
  position: relative;
  color: #f1b24b;
  text-decoration: none;
}
a:before {
  content: "";
  position: absolute;
  top: 105%;
  left: 0;
  height: 1px;
  width: 100%;
  background: white;
  opacity: 0;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: scale(0, 1);
      -ms-transform: scale(0, 1);
          transform: scale(0, 1);
}
a:after {
  content: "";
  position: absolute;
  left: 100%;
  background: white;
  top: 105%;
  opacity: 0;
  width: 1px;
  height: 10px;
  -webkit-transform: scale(1, 0) rotate(45deg);
      -ms-transform: scale(1, 0) rotate(45deg);
          transform: scale(1, 0) rotate(45deg);
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
}
a:hover:before {
  opacity: 1;
  -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
          transform: scale(1, 1);
  -webkit-transition: 0.25s all;
  transition: 0.25s all;
}
a:hover:after {
  opacity: 1;
  -webkit-transform: scale(1, 1) rotate(45deg);
      -ms-transform: scale(1, 1) rotate(45deg);
          transform: scale(1, 1) rotate(45deg);
  -webkit-transition: 0.15s 0.13s all;
  transition: 0.15s 0.13s all;
}
small {
  font-size: 0.75em;
}
#app {
  width: 90%;
  margin: 1em auto 0;
  max-width: 1200px;
}
.demo-settings {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 3em;
}
.demo-settings > div {
  width: 50%;
}
.code {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 1em;
}
.code > * {
  width: 48%;
}
.code__preview {
  text-align: center;
  font-size: 0.8em;
}
.props__item {
  margin: 1em 0;
}
table {
  width: 100%;
  border-collapse: collapse;
}
th {
  padding: 1em 0.5em;
  font-weight: bold;
  border-bottom: 1px solid white;
}
td {
  width: 50%;
  padding: 1em 0.5em;
  border-bottom: 1px solid white;
  vertical-align: top;
}
td:not(:first-child) {
  background: rgba(255, 255, 255, 0.1);
  border-left: 1px solid white;
}
label {
  display: block;
  margin-bottom: 1em;
}
.preview {
  margin-bottom: 2em;
  background: rgba(255, 255, 255, 0.1);
  padding: 1.5em 1.5em 1.5em 1.5em;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}
.preview__wrapper {
  width: 100%;
  font-size: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.preview__svg {
  background: rgba(0, 0, 0, 0.2);
  width: 1em;
  height: 1em;
  cursor: pointer;
}
svg {
  width: 1em;
  height: 1em;
}



